import { Suspense, lazy } from "react";
import { useLocation, useSearchParams } from "react-router-dom";
import InstituteNavbarConfig from "../Config/InstituteNavbarConfig";
import InstituteMemberConfig from "../Config/InstituteMemberConfig";
import QvipleLoader from "../../Loader/QvipleLoader";
import GoBack from "../../Shared/StaffStudentProfile/GoBack";
const StudentFeedback = lazy(() => import("./StudentFeedback/StudentFeedback"));
const StudentFeedbackChart = lazy(() =>
  import("./StudentFeedback/StudentFeedbackChart")
);
const StudentFeedbackDetail = lazy(() =>
  import("./StudentFeedback/StudentFeedbackDetail/StudentFeedbackDetail")
);

const InstituteFeedback = () => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  return (
    <InstituteNavbarConfig>
      <InstituteMemberConfig>
        <section
          style={{
            width: "100%",
          }}
        >
          <GoBack
            customStyle={{
              width: "100%",
              marginTop: "0",
              marginBottom: "1rem",
            }}
          />
          <Suspense fallback={<QvipleLoader />}>
            {searchParams.get("a") === "feedback-detail" ? (
              <StudentFeedbackDetail
                instituteId={getQuery.state?.instituteId}
              />
            ) : searchParams.get("a") === "feedback-analytic" ? (
              <StudentFeedbackChart
                instituteId={getQuery.state?.instituteId}
                ifid={getQuery.state?.ifid}
              />
            ) : (
              <StudentFeedback instituteId={getQuery.state?.instituteId} />
            )}
          </Suspense>
        </section>
      </InstituteMemberConfig>
    </InstituteNavbarConfig>
  );
};

export default InstituteFeedback;
